@import '../styles.css';

.skeleton .galleryButton {
	border-radius: 8px;
}

.skeleton .galleryImage {
	border-radius: 8px;
}

.skeleton span {
	animation: loading 1.5s infinite;
}

@keyframes loading {
	0% {
		opacity: 0.6;
	}
	50% {
		opacity: 1;
	}
	100% {
		opacity: 0.6;
	}
}
